.description__item {
  display: flex;
  gap: 20px;
  position: relative;
}
.description__item::before {
  content: counter(num);
  counter-increment: num;
  display: inline-block;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
  width: 134px;
  color: #f2f2f2;
  text-align: center;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 280px;
  font-weight: 400;
