.description__icon {
  position: relative;
  width: 60px;
  height: 60px;
  display: block;
  background-color: #68b738;
  flex-shrink: 0;
}
@media (min-width: 768px) {
  .description__icon {
    width: 80px;
    height: 80px;
  }
}
.description__icon::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-size: 100% 100%;
  background-repeat: no-repeat;
}
.description__icon--leaf::after {
  background-image: url("../img/stack.svg#icon_leaf");
  width: 30px;
  height: 32px;
}
.description__icon--bowl::after {
  background-image: url("../img/stack.svg#icon-bowl");
  width: 27px;
  height: 29px;
}
.description__icon--eat::after {
